<template>
    <div>
        <ul class="plugin-list">
            <li>
                <h3>Name：</h3>
                <div class="text">Toast</div>
            </li>
            <li>
                <h3>Introduction：</h3>
                <div class="text">
                    轻量的提示框，并且会在2秒之后自动消失。可以用来显示一些不会打断用户操作的提示
                </div>
            </li>
            <li>
                <h3>Demo：</h3>
                <p class="btn" @click.stop="onClickDemo()">demo</p>
            </li>
            <li>
                <h3>Usage：</h3>
<pre class="JS">
mobileModal.toast('提示内容');

</pre>
            </li>
            <li>
                <h3>Path：</h3>
                <div class="text">
                    文件路径：
                    'src/lib/mobileModal/mobileModal.css'
                    'src/lib/mobileModal/mobileModal.js'
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import mobileModal from '../../lib/mobileModal/mobileModal.js'
    export default {
        name: 'toast',
        data(){
            return {
                nowVal: ''
            }
        },
        methods: {
            onClickDemo(){
                mobileModal.toast('提示内容');
            }
        }
    }
</script>

<style scoped>
    @import "../../lib/mobileModal/mobileModal.css";
</style>


