<template>
    <div>
        <ul class="plugin-list">
            <li>
                <h3>Name：</h3>
                <div class="text">Picker</div>
            </li>
            <li>
                <h3>Introduction：</h3>
                <div class="text">移动端的选择器插件</div>
            </li>
            <li>
                <h3>Demo（选择器）：</h3>
                <p class="btn" @click.stop="picker1()">demoData1</p>
                <p class="btn" @click.stop="picker2()">demoData2</p>
                <p class="btn" @click.stop="picker3()">demoData3</p>
                <div class="text">
                    您选择的日期为：<input type="text" readonly="readonly" v-model="nowVal">
                </div>
            </li>
            <li>
                <h3>Usage：</h3>
<pre class="JS">
// 一级data
let demoData1 = [{
    "id": "1",
    "text": "北京",
    "data": ""
},{
    "id": "2",
    "text": "上海",
    "data": ""
}];
</pre>

<pre class="JS">
// 二级data
let demoData2 = [{
    "id": "1",
    "text": "北京",
    "data": [{
        "id": "11",
        "text": "北京市",
        "data": ""
    }]
},{
    "id": "2",
    "text": "上海",
    "data": [{
        "id": "21",
        "text": "上海市",
        "data": ""
    }]
}];
</pre>

<pre class="JS">
// 三级data
let demoData3 = [{
    "id": "1",
    "text": "北京",
    "data": [{
        "id": "11",
        "text": "北京市",
        "data": [{
            "id": "111",
            "text": "朝阳区",
            "data": ""
        }]
    }]
},{
    "id": "2",
    "text": "上海",
    "data": [{
        "id": "21",
        "text": "上海市",
        "data": [{
            "id": "211",
            "text": "浦东新区",
            "data": ""
        }]
    }]
}];
</pre>

<pre class="JS">
// 选择器
Picker.Init({
    //数组数据(必传)
    "data": demoData1,

    //标题(必传)
    "title": '请选择',

    //默认值（可选）
    "defaultValue": self.nowVal,

    //数组内的键名称(必传，id、text、data)
    "keys": {
        "id": "id",
        "value": "text",
        "childData": "data"//最多3级联动
    },

    "callBack": function (val) {
        // 回调函数（val为选择的值）
        self.nowVal = val;
    }
});
</pre>

            </li>
            <li>
                <h3>Path：</h3>
                <div class="text">
                    文件路径：</br>
                    'src/lib/picker/picker.css'
                    'src/lib/picker/picker.js'
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import Picker from '../../lib/picker/picker.js'

	export default {
		name: 'picker',
        data(){
            return {
                nowVal: ''
            }
        },
        methods: {
            // 选择器
            picker1: function () {
                var self = this;

                // 一级data
                let demoData1 = [{
                    "id": "1",
                    "text": "北京",
                    "data": ""
                },{
                    "id": "2",
                    "text": "上海",
                    "data": ""
                }];

                Picker.Init({
                    "data": demoData1,//数组数据(必传)
                    "title": '请选择',//标题(必传)
                    "defaultValue": self.nowVal,//默认值（可选）
                    "keys": {
                        "id": "id",
                        "value": "text",
                        "childData": "data"//最多3级联动
                    },//数组内的键名称(必传，id、text、data)
                    "callBack": function (val) {
                        //回调函数（val为选择的值）
                        self.nowVal = val;
                    }
                });
            },

            picker2: function () {
                var self = this;

                // 二级data
                let demoData2 = [{
                    "id": "1",
                    "text": "北京",
                    "data": [{
                        "id": "11",
                        "text": "北京市",
                        "data": ""
                    }]
                },{
                    "id": "2",
                    "text": "上海",
                    "data": [{
                        "id": "21",
                        "text": "上海市",
                        "data": ""
                    }]
                }];

                Picker.Init({
                    "data": demoData2,//数组数据(必传)
                    "title": '请选择',//标题(必传)
                    "defaultValue": self.nowVal,//默认值（可选）
                    "keys": {
                        "id": "id",
                        "value": "text",
                        "childData": "data"//最多3级联动
                    },//数组内的键名称(必传，id、text、data)
                    "callBack": function (val) {
                        //回调函数（val为选择的值）
                        self.nowVal = val;
                    }
                });
            },

            picker3: function () {
                var self = this;

                // 三级data
                let demoData3 = [{
                    "id": "1",
                    "text": "北京",
                    "data": [{
                        "id": "11",
                        "text": "北京市",
                        "data": [{
                            "id": "111",
                            "text": "朝阳区",
                            "data": ""
                        }]
                    }]
                },{
                    "id": "2",
                    "text": "上海",
                    "data": [{
                        "id": "21",
                        "text": "上海市",
                        "data": [{
                            "id": "211",
                            "text": "浦东新区",
                            "data": ""
                        }]
                    }]
                }];

                Picker.Init({
                    "data": demoData3,//数组数据(必传)
                    "title": '请选择',//标题(必传)
                    "defaultValue": self.nowVal,//默认值（可选）
                    "keys": {
                        "id": "id",
                        "value": "text",
                        "childData": "data"//最多3级联动
                    },//数组内的键名称(必传，id、text、data)
                    "callBack": function (val) {
                        //回调函数（val为选择的值）
                        self.nowVal = val;
                    }
                });
            }
        }
	}
</script>

<style scoped>
    @import "../../lib/picker/picker.css";
</style>


