<template>
    <div>
        <ul class="plugin-list">
            <li>
                <h3>Name：</h3>
                <div class="text">DatePicker</div>
            </li>
            <li>
                <h3>Introduction：</h3>
                <div class="text">移动端的日期选择器插件</div>
            </li>
            <li>
                <h3>Demo：</h3>
                <p class="btn" @click.stop="onClickDate()">demo</p>
                <div class="text">
                    您选择的日期为：<input type="text" readonly="readonly" v-model="nowVal">
                </div>
            </li>
            <li>
                <h3>Usage：</h3>

<pre class="JS">
DatePicker.Init({
    //0年, 1年月, 2月日, 3年月日
    "type": "3",

    //标题(必选)
    "title": '请选择日期',

    //最大年份（可选）
    "maxYear": "2100",

    //最小年份（可选）
    "minYear": "1900",

    //日期分割符(可选)
    "separator": "-",

    //默认值（可选）
    "defaultValue": '',

    "callBack": function (val) {
        //回调函数（val为选中的日期）
        self.nowVal = val;
    }
});
</pre>
            </li>
            <li>
                <h3>Path：</h3>
                <div class="text">
                    文件路径：</br>
                    'src/lib/datePicker/picker.css'
                    'src/lib/datePicker/datePicker.js'
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import DatePicker from '../../lib/datePicker/datePicker.js'

	export default {
		name: 'datePicker',
        data(){
            return {
                nowVal: ''
            }
        },
        methods: {
            // 日期选择器
            onClickDate(){
                var self = this;
                DatePicker.Init({
                    "type": "3",//0年, 1年月, 2月日, 3年月日
                    "title": '请选择日期',//标题(可选)
                    "maxYear": "2100",//最大年份（可选）
                    "minYear": "1900",//最小年份（可选）
                    "separator": "-",//分割符(可选)
                    "defaultValue": self.nowVal,//默认值（可选）
                    "callBack": function (val) {
                        //回调函数（val为选中的日期）
                        self.nowVal = val;
                    }
                });
            }
        }
	}
</script>

<style scoped>
    @import "../../lib/datePicker/picker.css";
</style>


