<template>
<div>
        <ul class="plugin-list">
            <li>
                <h3>Name：</h3>
                <div class="text">Alert</div>
            </li>
            <li>
                <h3>Introduction：</h3>
                <div class="text">确认对话框</div>
            </li>
            <li>
                <h3>Demo：</h3>
                <p class="btn" @click.stop="onClickDemo()">demo</p>
                <div class="text">
                    您点击了：<input type="text" readonly="readonly" v-model="nowVal">
                </div>
            </li>
            <li>
                <h3>Usage：</h3>
<pre class="JS">
mobileModal.alert(
    '提示内容',//必填

    //回调函数(可选)
    function () {
        self.nowVal = '确定按钮';
    }
);
</pre>
            </li>
            <li>
                <h3>Path：</h3>
                <div class="text">
                    文件路径：
                    'src/lib/mobileModal/mobileModal.css'
                    'src/lib/mobileModal/mobileModal.js'
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import mobileModal from '../../lib/mobileModal/mobileModal.js'
    export default {
		name: 'alert',
        data(){
            return {
                nowVal: ''
            }
        },
        methods: {
            onClickDemo(){
                var self = this;
                mobileModal.alert('提示内容', function () {
                    //回调
                    self.nowVal = '确定按钮';
                });
            }
        }
	}
</script>

<style scoped>
    @import "../../lib/mobileModal/mobileModal.css";
</style>


